.vk-skeleton-content {
  position: relative;
  overflow: hidden;
  width: 100%;
  min-width: 10px;
  height: 8px;
  background: #f0f0f0;
  border-radius: 3px;
}

.vk-skeleton--animated .vk-skeleton-content::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  max-width: 1000px;
  height: 100%;
  background: -webkit-gradient(linear, left top, right top, from(transparent), color-stop(15%, #f8f8f8), color-stop(30%, transparent));
  background: linear-gradient(to right, transparent 0%, #f8f8f8 15%, transparent 30%);
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-name: vkSkeletonAnimation;
          animation-name: vkSkeletonAnimation;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

/*
 * Animation
 */

@-webkit-keyframes vkSkeletonAnimation {
  0% {
    -webkit-transform: translate3d(-30%, 0, 0);
            transform: translate3d(-30%, 0, 0);
  }
  100% {
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }
}

@keyframes vkSkeletonAnimation {
  0% {
    -webkit-transform: translate3d(-30%, 0, 0);
            transform: translate3d(-30%, 0, 0);
  }
  100% {
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }
}

.vk-skeleton-text {
  display: inline-block;
}

[class^="vk-skeleton-"] + .vk-skeleton-text {
  margin-top: 20px;
}

.vk-skeleton-text:nth-of-type(4n + 1) {
  width: 80%;
}

.vk-skeleton-text:nth-of-type(4n + 2) {
  width: 100%;
}

.vk-skeleton-text:nth-of-type(4n + 3) {
  width: 70%;
}

.vk-skeleton-text:nth-of-type(4n + 4) {
  width: 85%;
}

.vk-skeleton-text:only-of-type {
  width: 100%;
}

.vk-skeleton-title {
  width: 85%;
  display: inline-block;
  margin-bottom: 10px;
}

.vk-skeleton-title .vk-skeleton-content {
  background: #dfdfdf;
}

.vk-skeleton-title + .vk-skeleton-text {
  width: 90%;
  margin-top: 0;
}

.vk-skeleton-image .vk-skeleton-content {
  height: 100%;
}

